.card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  max-width: 578px;
}

.imgBlock {
  text-align: center;
  padding-bottom: 25px;
}

.imgBlock h3 {
  padding-bottom: 35px;
  font-weight: 700;
  font-size: 3.0rem;
  line-height: 167%;
  color: #028CA6;
}

.img {
  max-width: 300px;
  filter: drop-shadow(0px 4px 25px rgba(6, 140, 166, 0.25));
}

@media(max-width: 1023.9px) {
  .card {
    padding-bottom: 15px;
  }
}